/*
* === GLOBAL ===
*/
*,
*:before,
*:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    color: rgb(35, 31, 32);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue, sans-serif;
    font-size: calc(112.5% + 0.5vw);
    margin: 0;
    padding: 0;
}

a {
    color: rgb(0, 174, 239);
}

pre {
    border: 1px solid lightgray;
    margin: 50px auto 0;
    max-width: 100%;
    padding: 1em;
    text-align: left;
    white-space: pre-wrap;
    width: 300px;
    word-wrap: break-word;
}

pre code {
    font-size: 13px;
}

/*
* === HEADER ===
*/

header.header {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    left: 0;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    height: 100px;
    max-width: 1100px;
    margin: 0 auto;
    padding: 15px 12px;
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    z-index: 10;
}

@media (min-width: 801px) {
    header.header {
        position: fixed;
    }
}

#brand {
    height: auto;
    -webkit-transition: all 123ms ease-in-out;
    transition: all 123ms ease-in-out;
    width: 45px;
}

@media (min-width: 801px) {
    #brand {
        width: 75px;
    }
}

#brand svg {
    fill: rgb(35, 31, 32);
}

.social-links ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    margin: 0;
    padding: 0;
    position: relative;
}

.social-links li {
    list-style-type: none;
    margin: 0;
}

.social-links a {
    color: rgb(210, 210, 210);
    display: block;
    height: 45px;
    margin: 0;
    padding: 0;
    position: relative;
    -webkit-transition: all 123ms ease-in-out;
    transition: all 123ms ease-in-out;
    width: 45px;
}

@media (min-width: 801px) {
    .social-links a {
        color: rgb(35, 31, 32);
        height: 32px;
        width: 32px;
    }
}

.social-links a:hover svg {
    fill: rgb(0, 174, 239);
}

@media (min-width: 801px) {
    .social-links a:hover .info {
        visibility: visible;
    }
}

.social-links .info {
    color: rgb(236, 0, 140);
    font-size: 9px;
    font-weight: lighter;
    left: -20px;
    position: absolute;
    text-align: center;
    top: -10px;
    -webkit-transition: all 23ms ease-in-out;
    transition: all 23ms ease-in-out;
    right: -20px;
    visibility: hidden;
}

.social-links svg {
    fill: currentColor;
    height: 100%;
    margin: 0;
    -webkit-transition: all 123ms ease-in-out;
    transition: all 123ms ease-in-out;
    width: 100%;
}

.more-info {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    bottom: 32px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    left: 0;
    position: absolute;
    margin: 0 auto;
    max-width: 1100px;
    right: 0;
    width: calc(100% - 30px);
    z-index: 10;

}

.email {

    font-size: 11px
}

.email a {
    color: rgb(35, 31, 32);
    position: relative;
    text-decoration: none;
}
.email a:before {
    background-color: rgb(236, 0, 140);
    bottom: -4px;
    content: '';
    left: 0;
    height: 2px;
    opacity: 0.5;
    position: absolute;
    -webkit-transition: width 123ms ease-in-out;
    transition: width 123ms ease-in-out;
    width: 0;
}

.email a:hover:before {
    width: 100%;
}

.resume-link {

    font-size: 12px
}

.resume-link a {
    color: rgb(236, 0, 140);
}


/*
* === BODY ===
*/

main {
    position: relative;
    width: 100%;
}

section {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    height: 100vh;
    margin: 0 auto;
    max-width: 1100px;
    padding: 22px 0;
    position: relative;
    z-index: 1;
}

/*
* === TYPOGRAPHY ===
*/

h1,
h2,
h3 {
    font-weight: normal;
    letter-spacing: 2px;
    line-height: 1.2;
    margin: 0;
    text-transform: lowercase;
}

h1 {
    font-size: 58px;
    font-weight: bold;
    line-height: 0.8;
}

@media (min-width: 801px) {
    h1 {
        font-size: 7.5vw;
    }
}

h2 {
    font-size: 19px;
}

@media (min-width: 801px) {
    h2 {
        font-size: 1.75vw;
    }
}


/*
* === INTRODUCTION ===
*/

.intro {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.intro h2 {
    line-height: 1.6;
    margin: 0 auto;
    word-wrap: break-word;
    width: 80vw;
}

.intro .content {
    position: relative;
    text-align: center;
    z-index: 3;
}

.shapes {
    bottom: 25vh;
    left: 0;
    height: auto;
    margin: 0 auto;
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100vw - 112px);
}

@media (min-width: 801px) {
    .shapes {
        width: 86vw;
    }
}

.shapes svg {
    height: 100%;
    width: 100%;
}

.layer svg g {
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
}


/*
* === CONTENT ===
*/
.ssh {
    padding: 0 0 3em 0;
}


/*
* === Resume Page ===
*/

.resume {
    -webkit-box-shadow: 0 0 12px rgba(0, 0, 0, .25);
            box-shadow: 0 0 12px rgba(0, 0, 0, .25);
    margin: 104px auto 48px;
    width: 100%;
}

@media (min-width: 801px) {
    .resume {
        max-width: 1100px;
    }
}



/*
* === FOOTER ===
*/
.footer-bar {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 11px;
}

.footer-bar-item {
    -webkit-box-flex: 1;
        -ms-flex: 1 1;
            flex: 1 1;
    height: 100%;
    opacity: 0.5
}

.footer-bar-item:first-child {
    background-color: rgb(0, 174, 239);
}

.footer-bar-item:nth-child(2) {
    background-color: rgb(236, 0, 140);
}

.footer-bar-item:nth-child(3) {
    background-color: rgb(255, 242, 0);
}

.today-msg {
    font-size: 12px;
    line-height: 1.4;
    margin: 0 15px 17px;
    text-align: right;
}


/*
* === LOADING SCREEN ===
*/
.loader:empty,
.loader:empty:before,
.loader:empty:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation: load 1.8s infinite ease-in-out;
          animation: load 1.8s infinite ease-in-out;
}

.loader:empty {
    color: cyan;
    font-size: 10px;
    margin: 80px auto;
    position: relative;
    text-indent: -9999em;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-animation-delay: -0.16s;
            animation-delay: -0.16s;
}

.loader:empty:before, .loader:empty:after {
    content: '';
    position: absolute;
    top: 0;
}

.loader:empty:before {
    color: magenta;
    left: -3.5em;
    -webkit-animation-delay: -0.32s;
            animation-delay: -0.32s;
}

.loader:empty:after {
    color: yellow;
    left: 3.5em;
}

@-webkit-keyframes load {
    0%, 80%, 100% {
        -webkit-box-shadow: 0 2.5em 0 -1.3em;
                box-shadow: 0 2.5em 0 -1.3em;
    }

    40% {
        -webkit-box-shadow: 0 2.5em 0 0;
                box-shadow: 0 2.5em 0 0;
    }
}

@keyframes load {
    0%, 80%, 100% {
        -webkit-box-shadow: 0 2.5em 0 -1.3em;
                box-shadow: 0 2.5em 0 -1.3em;
    }

    40% {
        -webkit-box-shadow: 0 2.5em 0 0;
                box-shadow: 0 2.5em 0 0;
    }
}



